import HeaderMenu from "@/components/Layout/HeaderMenu";
import Welcome from "@/components/Pages/Welcome";
import SecondWar from "@/components/Pages/SecondWar";
import SeptemberSeventeenth from "@/components/Pages/17_09_1939";
import Soviet from "@/components/Pages/Soviet";
import SikorskiMajski from "@/components/Pages/SikorskiMajski";
import Zsrs from "@/components/Pages/Zsrs";
import PolandInAfrica from "@/components/Pages/PolandInAfrica";
import CreateHistory from "@/components/Pages/CreateHistory";
import GenAnders from "@/components/Pages/GenAnders";
import ItalyAliants from "@/components/Pages/ItalyAliants";
import BeforyPoland from "@/components/Pages/BeforePoland";
import StartWar from "@/components/Pages/StartWar";
import WarMonteCassino from "@/components/Pages/WarMonteCassino";
import PolandBanner from "@/components/Pages/PolandBanner";
import SecondCassino from "@/components/Pages/SecondCassino";

