                    component: Welcome,
                    title: 'Witamy na stronie',
                    link: '#welcome'
                },
                {
                    component: SecondWar,
                    title: 'Wybuch II wojny światowej',
                    link: '#war',
                    description: {
                        link: '/secondWar/description'
                    }
                },
                {
                    component: SeptemberSeventeenth,
                    title: '17 września 1939',
                    link: '#1939'
                },
                {
                    component: Soviet,
