                    title: 'Ewakuacja Armii Andersa z ZSRS',
                    link: '#ZSRS'
                },
                {
                    component: PolandInAfrica,
                    title: 'Polacy w Afryce i na Bliskim Wschodzie',
                    link: '#poland-in-africa'
                },
                {
                    component: CreateHistory,
