import {createWebHistory, createRouter} from "vue-router";
import SecondWarDescription from "@/components/Pages/SecondWar/SecondWarDescription";
import Home from "@/components/Pages/Home";

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
    },
    {
        path: "/secondWar/description",
        name: "SecondWarDescription",
        component: SecondWarDescription,
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes: routes,
    scrollBehavior(to) {
        if (to.hash) {
            return {
                selector: to.hash
            };
        }
    },
});

export default router;